* {
  box-sizing: border-box; }

body {
  font-family: Microsoft JhengHei; }

.wrap {
  width: 100%;
  margin: 0 auto; }

section {
  padding-top: 25px;
  padding-bottom: 25px; }

.course-content {
  padding-left: 30px;
  padding-right: 30px; }
  .course-content .slider {
    margin: 20px auto; }

.slick-slide {
  margin: 12px;
  width: 350px; }
  @media (max-width: 768px) {
    .slick-slide {
      width: 350px; } }
  @media (max-width: 568px) {
    .slick-slide {
      width: 220px;
      margin: 12px 6px; } }
  .slick-slide img {
    width: 100%;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.25); }

.slick-prev:before,
.slick-next:before {
  color: black;
  font-size: 24px; }

.course-search {
  display: inline-block;
  margin-right: 10px; }
  .course-search button {
    padding-left: 30px;
    padding-right: 30px; }
  @media (max-width: 768px) {
    .course-search {
      display: none; } }

.btn-custom {
  background-color: #37761D;
  border: 1px solid #37761D;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px; }

.search-upgrade {
  display: none; }
  @media (max-width: 768px) {
    .search-upgrade {
      display: block;
      float: right;
      padding-top: 10px; } }
  .search-upgrade .modal-body > p {
    display: inline-block; }
  .search-upgrade .modal-body .dropdown {
    display: inline-block;
    margin-bottom: 10px; }
    @media (max-width: 414px) {
      .search-upgrade .modal-body .dropdown {
        display: block; } }

.course-box {
  padding-bottom: 80px; }
  .course-box h2 {
    display: inline;
    font-size: 36px;
    border-bottom: 5px solid #f05f40;
    padding-bottom: 5px; }

.my-course-card {
  margin-top: 20px;
  padding-top: 1px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.95); }
  .my-course-card h3 {
    font-size: 36px;
    text-align: center;
    font-weight: bold;
    padding-top: 10px;
    padding-bottom: 15px;
    text-shadow: 2px 2px 3px gray; }
  .my-course-card center a {
    margin-top: 10px;
    margin-bottom: 30px;
    margin-right: 5px; }
  .my-course-card center .btn-pay {
    background-color: #37761D; }
    .my-course-card center .btn-pay:hover {
      background-color: #4a9f27;
      border: 1px solid #4a9f27; }

.card-inform {
  border-radius: 30px;
  min-height: 250px;
  margin-left: 15px;
  margin-right: 15px;
  position: relative; }
  .card-inform .card-status {
    display: inline-block;
    background-color: #4da529;
    padding: 5px 10px;
    border-radius: 10px;
    position: absolute;
    right: 20px;
    bottom: 20px; }
    .card-inform .card-status > a {
      font-size: 24px;
      font-weight: bold;
      color: #fff; }

.first-lesson {
  background-image: url(../img/uian.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .first-lesson {
      height: 400px; } }

.second-lesson {
  background-image: url(../img/hankcoach.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .second-lesson {
      height: 400px; } }

.third-lesson {
  background-image: url(../img/wenyen2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .third-lesson {
      height: 400px; } }

.forth-lesson {
  background-image: url(../img/coachS_Mona.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .forth-lesson {
      height: 400px; } }

.course-list {
  list-style: none;
  margin-top: 20px;
  padding-right: 30px; }
  .course-list > li > ul > li {
    display: inline-block;
    list-style-type: circle;
    padding: 10px 0;
    border-bottom: 1px solid #D5D8DC;
    font-size: 14px; }
